<script setup lang="ts">
import type { FormInstance, FormRules } from 'element-plus'
import storage from '@/utils/storage'
import { ElMessage } from 'element-plus'
import { useI18n } from 'vue-i18n'

defineOptions({
  name: 'ResetPasswordForm',
})

const props = defineProps<{
  account?: string
}>()

const emits = defineEmits<{
  onLogin: [account: string]
  onResetPassword: [account: string]
}>()

const { t } = useI18n()

const loading = ref(false)

const formRef = useTemplateRef<FormInstance>('formRef')
const form = ref({
  account: props.account ?? storage.local.get('login_account') ?? '',
  captcha: '',
  newPassword: '',
})
const rules = ref<FormRules>({
  account: [
    { required: true, trigger: 'blur', message: () => t('rules.account') },
  ],
  captcha: [
    { required: true, trigger: 'blur', message: () => t('rules.captcha') },
  ],
  newPassword: [
    { required: true, trigger: 'blur', message: () => t('rules.newPassword') },
    { min: 6, max: 18, trigger: 'blur', message: () => t('rules.newPasswordLength') },
  ],
})
function handleReset() {
  ElMessage({
    message: 'é‡ç½®å¯†ç ä»…æä¾›ç•Œé¢æ¼”ç¤ºï¼Œæ— å®é™…åŠŸèƒ½ï¼Œéœ€å¼€å‘è€…è‡ªè¡Œæ‰©å±•',
    type: 'info',
  })
  formRef.value?.validate((valid) => {
    if (valid) {
      // è¿™é‡Œç¼–å†™ä¸šåŠ¡ä»£ç 
      emits('onResetPassword', form.value.account)
    }
  })
}
</script>

<template>
  <ElForm ref="formRef" :model="form" :rules="rules" class="min-h-500px w-full flex-col-stretch-center p-12">
    <h3 class="mb-8 text-xl color-[var(--el-text-color-primary)] font-bold">
      {{ t('intro') }}
    </h3>
    <div>
      <ElFormItem prop="account">
        <ElInput v-model="form.account" size="large" :placeholder="t('form.account')" type="text" tabindex="1">
          <template #prefix>
            <SvgIcon name="i-ri:user-3-fill" />
          </template>
        </ElInput>
      </ElFormItem>
      <ElFormItem prop="captcha">
        <ElInput v-model="form.captcha" size="large" :placeholder="t('form.captcha')" type="text" tabindex="2">
          <template #prefix>
            <SvgIcon name="i-ic:baseline-verified-user" />
          </template>
          <template #append>
            <ElButton>{{ t('form.sendCaptcha') }}</ElButton>
          </template>
        </ElInput>
      </ElFormItem>
      <ElFormItem prop="newPassword">
        <ElInput v-model="form.newPassword" type="password" size="large" :placeholder="t('form.newPassword')" tabindex="3" show-password>
          <template #prefix>
            <SvgIcon name="i-ri:lock-2-fill" />
          </template>
        </ElInput>
      </ElFormItem>
    </div>
    <ElButton :loading="loading" type="primary" size="large" style="width: 100%; margin-top: 20px;" @click.prevent="handleReset">
      {{ t('form.confirm') }}
    </ElButton>
    <div class="mt-4 flex-center gap-2 text-sm color-[var(--el-text-color-secondary)]">
      <ElLink type="primary" :underline="false" @click="emits('onLogin', form.account)">
        {{ t('login') }}
      </ElLink>
    </div>
  </ElForm>
</template>
